.bsa-cpc ._default_ {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0 1em;
  padding: 1em;
  width: 50%;
  border-radius: .5rem;
  background-color: #fff;
  line-height: 1.5
}

.bsa-cpc a {
  text-align: left;
  color: inherit;
  text-decoration: none;
  transition: border-color 0.15s ease-in-out, background-color 0.15s ease-in-out
}

.bsa-cpc a:hover {
  border-color: #adb5bd
}

.bsa-cpc .default-image img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  width: 36px;
  border-radius: 7.5%
}

.bsa-cpc .default-description, .bsa-cpc .default-title {
  display: block;
  margin-left: 46px;
  max-width: calc(100% - 36px)
}

.bsa-cpc .default-title {
  font-weight: 600
}

@media only screen and (min-width: 320px) and (max-width: 759px) {
  .bsa-cpc #_default_ {
    flex-wrap: wrap
  }

  .bsa-cpc ._default_ {
    margin: 0.5em 0;
    width: 100%
  }
}

